.center {
    width: 800px; /* Ширина элемента в пикселах */
    padding: 50px; /* Поля вокруг текста */
    padding-bottom: 30px;
    margin:  auto; /* Выравниваем по центру */
    margin-top: 30px;
    background: #FFECAB; /* Цвет фона */
    border-radius: 8px;
    font-family: Roboto, Arial, Helvetica, sans-serif; 
}
.formRow {
    min-height: 130px;
}

label{
    min-height: 100px;
}

p{
    font-size: 14px;
    line-height: 1.5;
    /* margin-top: 10px; */
}
h2{
    margin-top: 0px;
}
h3{
    margin-bottom: 0px;
}

.errorMessage{
    font-size: 14px;
    color: #FB2A44;
    margin-top: 8px;    
}



input[type='text'], input[type='email'], input[type='password'], textarea {
    width:  100%;

background: #fff;
border: 1px solid #ccc;
padding: 10px 10px;
-webkit-border-radius: 4px;
-moz-border-radius: 4px;
border-radius: 4px;
box-sizing:border-box;
-webkit-box-sizing: border-box;
-moz-box-sizing: border-box;
}

.button{
    background-color: #4A90E2; /* Green */
    border: none;
    border-radius: 4px;
    color: white;
    padding: 10px 16px;
    margin-top: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}

.button:hover {
    background-color: #3583DF; /* Green */
    color: white;
}